/*[[搜索下拉*/
.search-list{
    display: none;
    position: absolute;
    border: 1px solid #ddd;
    border-top: none;
    background: #fff;
    overflow: hidden;
    line-height:28px;
    left: -1px;
    right: -1px;
    top: 49px;
    font-size:12px;
    padding:0;
    z-index:999;
}
.search-list.search-empty{
    border:none;
}
.search-list a:hover{
    text-decoration: none;
}
.search-list .top-search-title{
    height:28px;
    line-height:28px;
    font-size:12px;
    color: #999;
    padding:5px 15px 0;
}
.search-info .search-item{
    display: block;
    padding:0 15px;
}
.search-info .del-search{
    display: none;
}
.search-info:hover,.search-info.active{
    background-color: #f2f2f2;
}
.search-info:hover .del-search,.search-info.active .del-search{
    display: block;
}
.search-info .search-text{
    float: left;
}
.top-search-title .del-search,.search-info .del-search{
    float: right;
    color: #d72245;
}
/*搜索下拉]]*/